.btn_git {
  color: white;
  background: none;
  border: 0;
  margin: 5px;
}

.btn_git:hover {
  transform: scale(1.05);
}

.div_git {
  text-align: start;
  width: 600px;
}

@media only screen and (max-width: 800px) {
  .div_git {
    text-align: start;
    width: 300px;
  }
}
@media only screen and (max-width: 600px) {
  .div_git {
    text-align: start;
    width: 320px;
  }
}
@media only screen and (max-width: 400px) {
  .div_git {
    text-align: start;
    width: 220px;
  }
}

