.text-animation {
  opacity: 0;
  font-size: 22px;
  transform: translateY(19px);
  justify-content: center;
  transition: opacity 5ms ease-in-out, transform 25ms ease-in-out;
  position: relative;
  vertical-align: middle;
  width: 565px;
  height: 500px;
  margin-bottom: 200px;
  display: flex;
  align-items: center;
}

.visible {
  opacity: 1;
  transform: translateY(0);
}

@media only screen and (max-width: 800px) {
  .text-animation {
    display: grid;
    justify-content: center;
    width: 450px;
    height: 100px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
  }
}
@media only screen and (max-width: 550px) {
  .text-animation {
    display: flex;
    justify-content: center;
    width: 300px;
    height: 100px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 70px;
  }
}
@media only screen and (max-width: 400px) {
  .text-animation {
    display: flex;
    justify-content: center;
    width: 200px;
    height: 100px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 70px;
  }
}

