.contact_main{
  display: flex;
  width: 600px;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.contact_img{
  width: 90px;
  height: 90px;
  padding: 25px;
}.contact_img:hover{
  transform: scale(0.95);
 }.contact_img:active{
    transform: scale(0.9);
   }

.contact_icon{
  display: flex;
  border-radius: 45px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contact_text{
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  text-decoration: none;
}.contact_text:hover{
  text-decoration: underline;
  cursor: pointer;
 }.contact_text:active{
    transform: scale(0.9);
   }

.contact_a{
  text-decoration: none;
}
@media only screen and (max-width: 500px) {

  .contact_main{
    display: flex;
    width: 400px;
    justify-content: center;
    align-content: center;
    align-items: center;
  }

  .contact_img{
    width: 60px;
    height: 60px;
    padding: 25px;
  }.contact_img:hover{
     transform: scale(0.95);
   }.contact_img:active{
      transform: scale(0.9);
    }
}