.App_Login{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75vh;
}
.App_bing{
  display: flex;
  width: 550px;
  height: 75vh;
  align-items: center;

}
.App_Button {
  margin: 50px;
  border: none;
  font-size: 18px;
  text-decoration: none;
  background: none;
  animation: shake 0.2s ease-in-out infinite,
  glitch 2s linear infinite;
}




@keyframes shake {
  0% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-1px, -1px);
  }
  20% {
    transform: translate(1px, -1px);
  }
  30% {
    transform: translate(-1px, 1px);
  }
  40% {
    transform: translate(1px, 1px);
  }
  50% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}



@keyframes glitch {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.9);
    opacity: 0.99;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  75% {
    transform: scale(0.99);
    opacity: 0.8;
  }
  100% {
    transform: scale(1.05);
    opacity: 1;
  }
}


.App_Button:hover {
  animation: shake 0.2s ease-in-out;
}
