.NavBase {
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 25px;
  margin-top: 15px;
}

.NavLink {
  text-decoration: none;
  font-family: "Dancing Script";
  font-size: 27px;
  padding: 25px;
  margin-right: 5px;
  position: relative;
  width: 70px;
}

.NavLink::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0;
  bottom: 15px;
  left: 0;
  background-color: white;
  transition: height 0.03s ease-out, opacity 0.5s ease-in;
  opacity: 0;
}

.NavLink.isActive::after {
  height: 2.5%;
  opacity: 1;
}

.NavLink:hover::after {
  height: 4%;
  opacity: 1;
}

