.Main {
  height: 350px;
  width: 700px;
  border: 3px solid white;
  border-radius: 20px;
  margin-top: 10px;
}

.data{
  text-align: end;
  align-items: end;
}

.git{
  margin-top: 15px;
  border-radius: 50px;
}


@media only screen and (max-width: 800px) {

  .Main {
    height: 350px;
    width: 450px;
    border: 3px solid white;
    border-radius: 20px;
    margin-top: 10px;
  }
}

@media only screen and (max-width: 500px) {

  .Main {
    height: 350px;
    width: 350px;
    border: 3px solid white;
    border-radius: 20px;
    margin-top: 10px;
  }
}


@media only screen and (max-width: 400px) {

  .Main {
    height: 350px;
    width: 250px;
    border: 3px solid white;
    border-radius: 20px;
    margin-top: 10px;
  }
}