@import "contact";
@import "about";
@import "main";
@import "/src/component/UI/NavBar/NavBar.module.css";
@import "/src/component/UI/TextAnimation/TextAnimation.css";
@import "/src/component/UI/Box/Box.module.css";
@import "/src/component/UI/Notify/Notification.css";
@import "/src/component/UI/Footer/Footer.module.css";
@import "/src/component/Album.module.css";
@import "loader";
@import "approutes";
@import "/src/component/Git/git.module.css";

$main-bg-color: #1C1C1C;

*{
  font-family: "Dancing Script";/* Yu Gothic */
  font-size: 21px;
  margin: 0;
  padding: 0;
  color: white;
  background: $main-bg-color;
  opacity: 1;
  justify-content: center;
  text-align: center;
}



.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: all 400ms;

}

.fade-exit {
  opacity: 0;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}

.Center_align{
  display: flex;
  justify-content: center;
  padding: 150px;
}

/* Стили для скроллбара */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: rgba(0,0,0,0);
  margin-top: 100px;
}

::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 5px;

}

::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

body {
  overflow-x: hidden;
}


.center{
  display: grid;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.center_flex{
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}
