.notification {
  position: fixed;
  bottom: 30px;
  right: 30px;
  border: 1px solid #ccc;
  border-radius: 9px;
  width: auto;
  height: auto;
  padding: 10px;
  margin-bottom: 10px;
  z-index: 9999;
  display: flex;
  opacity: 0;
  visibility: hidden;
  text-align: center;
  transition: opacity 0.5s ease-in-out, visibility 0.5s linear, height 0.5s ease-in-out, padding 0.5s ease-in-out;
  /* Add the following properties for text wrapping and overflow handling */
  white-space: normal;
  overflow-wrap: break-word;
}

.notification.visible {
  opacity: 1;
  visibility: visible;
  height: auto;
  padding: 10px;
}

.close_x {
  border-radius: 30px;
  border: none;
  font-size: 25px;
}

